.stage {
  overflow: hidden;
}

.stage.main {
  text-align: center;
  padding-top: 54px;
}

.stage.main .title {
  display: inline;
  font-size: 26px;
  line-height: .8em;
}

.stage.main .title p {
  margin-top: 12px;
}

.stage.main .title p:nth-of-type(1) strong:before {
  content: ">>";
  position: absolute;
  top: 0;
  left: -20px;
  font-size: 10px;
  font-weight: 300;
  line-height: 20px;
}

.stage.main .title p:nth-of-type(3) strong:before {
  content: "<<";
  position: absolute;
  top: 0;
  right: -20px;
  font-size: 10px;
  font-weight: 300;
  line-height: 20px;
}

.stage.main .title strong {
  position: relative;
  display: inline-block;
  padding-bottom: 3px;
  border-bottom: 3px solid #000;
  vertical-align: top;
}

.stage.main .date {
  font-style: italic;
  font-size: 10px;
  margin-top: 7px;
}

.stage.main .user-info {
  margin-top: 33px;
  display: flex;
  padding: 0 64px;
  align-content: center;
}

.stage.main .user-info .info {
  flex: 1;
  line-height: .8;
}

.stage.main .user-info .info span {
  display: block;
  font-size: 14px;
}

.stage.main .user-info .info strong {
  display: block;
  margin-top: 9px;
  font-size: 23px;
}

.stage.main .user-info .icon-heart {
  width: 49px;
  background: url("../../images/icon_heart.gif") no-repeat 0 0;
  background-size: 100%;
}

.stage.main .img-box {
  margin-top: 25px;
}

.stage.main .js-event1 {
  opacity: 0;
  transform: translate(0, 10px);
  transition: all 0.5s cubic-bezier(0.68, -0.55, 0.265, 1.55);
}

.stage.main .js-event1.active {
  opacity: 1;
  transform: translate(0, 0);
}

.stage.main .js-event2 {
  opacity: 0;
  transform: translate(0, 15px);
  transition: all 0.5s cubic-bezier(0.175, 0.885, 0.32, 1.275) 0.2s;
}

.stage.main .js-event2.active {
  opacity: 1;
  transform: translate(0, 0);
}

.stage.main .js-event3 {
  opacity: 0;
  transform: translate(0, 20px);
  transition: all 0.5s cubic-bezier(0.175, 0.885, 0.32, 1.275) 0.4s;
}

.stage.main .js-event3.active {
  opacity: 1;
  transform: translate(0, 0);
}

.stage.main .js-event4 {
  opacity: 0;
  transform: rotateY(100deg);
  transition: all 0.6s cubic-bezier(0.39, 0.575, 0.565, 1) 0.6s;
}

.stage.main .js-event4.active {
  opacity: 1;
  transform: rotateX(0deg);
}

.stage.main .js-event5 {
  opacity: 0;
  transform: rotateY(100deg);
  transition: all 0.7s cubic-bezier(0.39, 0.575, 0.565, 1) 0.8s;
}

.stage.main .js-event5.active {
  opacity: 1;
  transform: rotateX(0deg);
}

.stage.main .js-event6 {
  opacity: 0;
  transform: scale(0.8);
  transition: all 0.5s cubic-bezier(0.175, 0.885, 0.32, 1.275) 1s;
}

.stage.main .js-event6.active {
  opacity: 1;
  transform: scale(1);
}

.stage.main .js-event7 {
  opacity: 0;
  transform: translate(0, 10px);
  transition: all 1s cubic-bezier(0.39, 0.575, 0.565, 1) 1.2s;
}

.stage.main .js-event7.active {
  opacity: 1;
  transform: translate(0, 0);
}

